import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { UsuarioProvider } from './../../../providers/usuario-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    selector: 'usuario-detalle',
    template: require('./usuario-detalle.component.html'),
})
export class UsuarioDetalleComponent {
    private titleForm;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    roles;
    usuario;
    personales;
    usuarioDetalleForm: FormGroup;
    usuarioAddForm: FormGroup;
    formSubmitted = false;
    username: string;
    action: any;
    constructor(
        private usuarioProvider: UsuarioProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private location: Location) {
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.username = params['username'];
                this.action = params['action'];
            });
        this.titleForm = this.username ? 'Usuario ' : 'Agregar Usuario';
        if (!this.username) {
            this.getUsuarioPersonales();
        }
        else {
            this.buildForm();
            this.getUsuarioByUsername();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm;
                this.onDeleteForm();
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }
        }
    }

    getUsuarioByUsername() {
        this.titleForm == "";
        this.eeloading.open();
        this.usuarioProvider
            .GetUsuarioByUsername(this.username)
            .subscribe(
            data => {
                this.eeloading.close();
                this.roles = data.roles;
                this.usuario = data.usuario;
                this.usuarioDetalleForm.controls['username'].setValue(data.usuario.UserName);
                this.usuarioDetalleForm.controls['nombre'].setValue(data.usuario.nombre);
                this.usuarioDetalleForm.controls['email'].setValue(data.usuario.email);
                this.usuarioDetalleForm.controls['rol'].setValue(data.usuario.roleName);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.usuarioDetalleForm = this.formBuilder.group({
            username: this.formBuilder.control(null, [Validators.required]),
            nombre: this.formBuilder.control(null, [Validators.required]),
            email: this.formBuilder.control(null, [Validators.required]),
            rol: this.formBuilder.control(null, [Validators.required]),
            currentPassword: this.formBuilder.control(null, [Validators.required]),
            newPassword: this.formBuilder.control(null, [Validators.required]),
            personalID: this.formBuilder.control(null),
        });
    }

    enableForm() {
        // if (this.modeAddForm) { this.usuarioDetalleForm.controls['username'].enable(); }
        // else { this.usuarioDetalleForm.controls['username'].disable(); }
        this.usuarioDetalleForm.controls['username'].enable();
        this.usuarioDetalleForm.controls['email'].enable();
        this.usuarioDetalleForm.controls['personalID'].setValidators(Validators.required);
    }

    disableForm() {
        this.usuarioDetalleForm.controls['username'].disable();
        this.usuarioDetalleForm.controls['email'].disable();
        this.usuarioDetalleForm.controls['rol'].disable();
        this.usuarioDetalleForm.controls['nombre'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.usuarioAddForm = this.formBuilder.group({
            personalID: this.formBuilder.control(null, [Validators.required]),
            Username: this.formBuilder.control(null, [Validators.required]),
            Email: this.formBuilder.control(null, [Validators.required]),
            rol: this.formBuilder.control(null, [Validators.required]),
            Password: this.formBuilder.control(null, [Validators.required]),
        });
        this.formSubmitted = false;
    }

    getUsuarioPersonales() {
        this.eeloading.open();
        this.usuarioProvider
            .GetUsuariosPersonales()
            .subscribe(
            data => {
                this.eeloading.close();
                this.roles = data.roles;
                this.personales = data.personales;
                this.onAddForm();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onSubmit() {
        if (!this.usuarioDetalleForm.valid || this.formSubmitted) return;
        this.formSubmitted = true;
        let usuarioModel = this.usuarioDetalleForm.value;
        this.eeloading.open();
        if (this.modeAddForm) {
            //usuarioModel.id = this.id;
            this.usuarioProvider
                .PostUsuario(usuarioModel)
                .subscribe(
                data => {
                    this.formSubmitted = false;
                    this.eeloading.close();
                    this.eetoast.show('Usuario agregado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.formSubmitted = false;
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
        else if (this.modeEditForm) {
            //usuarioModel.periodoID = this.id;
            this.usuarioProvider
                .PutUsuario(usuarioModel)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Usuario actualizado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onAddSubmit() {
        if (!this.usuarioAddForm.valid || this.formSubmitted) return;
        this.formSubmitted = true;
        let usuarioModel = this.usuarioAddForm.value;
        usuarioModel.ConfirmPassword = usuarioModel.Password;
        let personalID = usuarioModel.personalID;
        this.eeloading.open();

        this.usuarioProvider
            .RegisterUsuario(usuarioModel, personalID)
            .subscribe(
            data => {
                this.formSubmitted = false;
                this.eeloading.close();
                this.eetoast.show('Usuario agregado', 'toast-succeed');
                this.location.back();
            },
            error => {
                this.formSubmitted = false;
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }

    onDeleteSubmit() {
        this.formSubmitted = true;
        this.eeloading.open();

        this.usuarioProvider
            .DeleteUsuario(this.usuario.UserId)
            .subscribe(
            data => {
                this.formSubmitted = false;
                this.eeloading.close();
                this.eetoast.show('Usuario Eliminado', 'toast-succeed');
                this.location.back();
            },
            error => {
                this.formSubmitted = false;
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }

    changeRol() {
        this.eeloading.open();
        this.usuarioProvider
            .ChangeRol(this.usuarioDetalleForm.controls['rol'].value, this.usuario.UserId)
            .subscribe(
            data => {
                this.eeloading.close();
                this.eetoast.show('Rol Modificado', 'toast-succeed');
                // this.location.back();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    changePassword() {
        this.eeloading.open();
        let body = {
            userId: this.usuario.UserId,
            currentPassword: this.usuarioDetalleForm.controls['currentPassword'].value,
            newPassword: this.usuarioDetalleForm.controls['newPassword'].value
        }
        this.usuarioProvider
            .ChangePassword(body)
            .subscribe(
            data => {
                this.eeloading.close();
                this.usuarioDetalleForm.controls['currentPassword'].setValue(null);
                this.usuarioDetalleForm.controls['newPassword'].setValue(null);
                this.eetoast.show('Contraseña Modificada', 'toast-succeed');
                // this.location.back();
            },
            error => {
                this.eeloading.close();
                this.eetoast.show('Contraseña actual no coincide', 'toast-error');
            }
            )
    }
    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.usuarioProvider
                .DeleteUsuario(this.username)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Usuario borrado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



