import { EEToast } from './ee-toast-provider';
import { DataStorageProvider } from './data-storage-provider';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Global } from './../variables/Global';
import { CanActivate, ActivatedRouteSnapshot, Router } from "@angular/router";

@Injectable()
export class AccessPageGuard implements CanActivate {
    constructor(
        private router: Router,
        private eetoast: EEToast,
        private dataStg: DataStorageProvider) { }
    canActivate(route: ActivatedRouteSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        const actualPage = route.component['name'];
        const coordPages = [
            'GastoComponent',
            'RendicionCuentaComponent',
            'CobroComponent',
            'PeriodosComponent',
            'CuotasComponent',
            'CuotaDetalleComponent',
            'ResidentesComponent',
            'ResidenteDetalleComponent',
            'PersonalesComponent',
            'PersonalResidenciaDetalleComponent',
            'PersonalDetalleComponent',
            'CobroComponent',
            'CargoComponent',
            'IngresoComponent',
            'PeriodoDetalleComponent',
            'ResumenCuentaComponent'];
        const jefaturaNotAllowed = [
            'AjusteComponent',
            'TransferenciaComponent',
            'TransferenciaCBComponent',
            'TransferenciaChequeComponent',
            'CobrosPendientesComponent',
            'UsuarioDetalleComponent',
            'UsuariosComponent',
            'ChequesEmitidosComponent',
            'ChequesComponent'
        ]
        let hasAccess = false;

        const rol = this.dataStg.getValue('rol');
        if (rol === 'COORD') {
            coordPages.forEach(element => {
                if (element === actualPage) hasAccess = true;
            });
        } else if (rol == 'JEFYC') {
            let pageInArray = true;
            jefaturaNotAllowed.forEach(element => {
                if (element === actualPage) pageInArray = false;
            });
            if (pageInArray) hasAccess = true;
        } else {
            hasAccess = true;
        }
        if (!hasAccess) {
            this.router.navigate(['app/dashboard']);
            this.eetoast.show('No tiene acceso', 'toast-error');
        }
        return true;
    }
}


